*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.home {
  text-align: center;
  background-color: #0000;
  background-image: linear-gradient(to right, #272727e6, #2727274d), url("model-1.9f8ae051.jpg");
  background-position: 0 0, top;
  background-repeat: repeat, repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  align-items: center;
  height: 100%;
  padding-top: 40vh;
  overflow: hidden;
}

.home__name {
  border-bottom: 2px solid #fff;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  font-size: 5rem;
}

.home__name--last {
  color: #ff652f;
  font-weight: 700;
}

.menu-btn {
  z-index: 1;
  cursor: pointer;
  width: 28px;
  height: 20px;
  transition: all .5s ease-in-out;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.menu-btn__hamburger {
  background: #fff;
  width: 28px;
  height: 3px;
  transition: all .5s ease-in-out;
  position: absolute;
  top: .5rem;
  right: 0;
}

.menu-btn__hamburger:before {
  content: "";
  background: #fff;
  width: 28px;
  height: 3px;
  transition: all .5s ease-in-out;
  position: absolute;
  top: -8px;
}

.menu-btn__hamburger:after {
  content: "";
  background: #fff;
  width: 20px;
  height: 3px;
  transition: all .5s ease-in-out;
  position: absolute;
  top: 8px;
}

.menu-btn__hamburger.open {
  background: none;
  transform: rotate(720deg);
}

.menu-btn__hamburger.open:before {
  transform: rotate(45deg)translate(5px, 8px);
}

.menu-btn__hamburger.open:after {
  width: 28px;
  transform: rotate(-45deg)translate(3px, -7px);
}

.nav {
  opacity: .98;
  visibility: hidden;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
}

.nav.open {
  visibility: visible;
}

.nav .menu-nav {
  background: #272727;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding-right: 1rem;
  list-style: none;
  transition: all .5s ease-in-out;
  display: flex;
  overflow: hidden;
  transform: translateY(-100%);
}

.nav .menu-nav.open {
  transform: translateY(0);
}

.nav .menu-nav__item {
  transition: all .5s ease-in-out;
  transform: translate(100vw);
}

.nav .menu-nav__item.open {
  transform: translateX(0);
}

.nav .menu-nav__item.active > a {
  color: #ff652f;
}

.nav .menu-nav__link {
  text-transform: uppercase;
  padding: 2rem 0;
  font-size: 2rem;
  font-weight: 300;
  transition: all .5s ease-in-out;
  display: inline-block;
}

.nav .menu-nav__link:hover {
  color: #ff652f;
}

.menu-nav__item:first-child {
  transition-delay: .25s;
}

.menu-nav__item:nth-child(2) {
  transition-delay: .35s;
}

.menu-nav__item:nth-child(3) {
  transition-delay: .45s;
}

.menu-nav__item:nth-child(4) {
  transition-delay: .55s;
}

.about {
