}

.projects__items {
  grid-gap: 2rem;
  grid-template-columns: 1fr;
  width: 60vw;
  margin: 2rem auto 0;
  display: grid;
}

.projects__item {
  cursor: pointer;
  border-bottom: 5px solid #ff652f;
  position: relative;
  overflow: hidden;
}

.projects__item img {
  width: 100%;
}

.projects__item:after {
  content: "";
  opacity: 0;
  background: #ff652f;
  width: 100%;
  height: 100%;
  transition: all .5s ease-in-out;
  position: absolute;
  top: 100%;
  left: 0;
}

.projects__item:hover:after {
  opacity: .9;
  top: 0;
}

.projects__item:hover .projects__btn {
  opacity: 1;
}

.projects__btns {
  z-index: 1;
  text-align: center;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  width: 100%;
  height: 100%;
  display: grid;
  position: absolute;
  top: 0;
}

.projects__btn {
  opacity: 0;
  color: #000;
  transition: all .5s ease-in-out;
}

.projects__btn:hover {
  color: #fff;
}

.projects .social-icons {
