@media screen and (width >= 768px) {
  .menu-btn {
    visibility: hidden;
  }

  .nav {
    visibility: visible;
  }

  .nav .menu-nav {
    text-align: right;
    background: none;
    height: 100%;
    display: block;
    transform: translateY(0);
  }

  .nav .menu-nav__item {
    padding-right: 1.5rem;
    display: inline;
  }

  .nav .menu-nav__item__link, .about__bio {
    font-size: 1.5rem;
  }

  .projects__bio-image {
    height: 40vh;
  }

  .projects__items {
    grid-template-columns: repeat(2, 1fr);
  }

  .projects .text-secondary {
    font-size: 3rem;
  }

  .contact__list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (width >= 1024px) {
  .projects__items, .contact__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (width >= 1600px) {
  .projects__bio-image {
    height: 50vh;
  }
}

